/* You can add global styles to this file, and also import other style files */
* {
    box-sizing: border-box;
}

@font-face {
    font-family: "digimon";
    src: url("/../../assets/fonts/Digivolve.otf") format("opentype");
}

body {
    -webkit-font-smoothing: antialiased;
}

body, html, ul, li, p {
    margin: 0;
    padding: 0;
}

label {
    font-family: digimon;
}
h1 {
    font-family: digimon;
}

app-root {
    float: left;
    width: 100%;
    height: 100%;
}

app-login {
    float: left;
    width: 100%;
    height: 100%;
}

